.heading {
  display: flex;
  justify-content: center;
  align-items: center;

  color: white;

  font-family: "Libre Baskerville", serif;
  flex-direction: column;
  margin-top: -50px;
  padding: 30px;

  @media screen and (max-width: 400px) {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 0px;
    margin-right: 0;
    overflow: hidden;
  }
}

.head1 {
  font-size: 50px;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  font-weight: bolder;
  font-style: normal;

  @media screen and (max-width: 400px) {
    font-size: 30px;
    margin-bottom: 40px;
  }
}

.main-grid {
  display: grid;
  grid-template-columns: auto auto auto;
  gap: 30px;

  @media screen and (max-width: 400px) {
    display: grid;
    grid-template-columns: auto;
    /* gap: 25px; */
    margin-left: -20px;
  
  }
}

.child-grid {
  border: 1px solid grey;
  padding: 20px;
  border-radius: 10px;

  @media screen and (max-width: 400px) {
    padding: 15px;
  }
}

.news-image {
  width: 100%;
  height: 300px;

  @media screen and (max-width: 400px) {
    height: 200px;
  }
}

.news-title {
  font-size: 26px;
  font-weight: 600;

  @media screen and (max-width: 400px) {
    font-size: 22px;
    font-weight: 500;
  }
}

.news-content {
  font-size: 17px;

  @media screen and (max-width: 400px) {
    font-size: 15px;
  }
}

.news-author {
  font-style: italic;
  font-size: 14px;
}

.news-date {
  font-size: 14px;
}

.space-between {
  display: flex;
  justify-content: space-between;
}

.link {
  color: whitesmoke;
  font-weight: 600;
}

.select {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 16px;
  margin-top: -40px;
  width: 260px;
  margin-top: 0%;
  margin-bottom: 50px;
  border-radius: 3px;

  @media screen  and (max-width:400px){

    margin-left: -32px;
    
  }
}

.select-box {
  margin-left: 5px;

  font-size: 24px;
  margin-top: -20px;


}

.options {
  font-size: 20px;
  margin-top: -20px;
}
